import { setCookie, getCookie } from "../assets/js/cookie.js"
export default {
	data() {
		return {
			showPrompting: false,
			prompting: false,
			username: '',
			password: '',
		}
	},
	mounted() {
		if(getCookie('uesrname')) {
			this.$router.push('/breakfast')
		}
	},
	methods: {
		login() {
			 var c = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[^]{8,16}$/
             var reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
			if(this.username == '' || this.password == '') {
				alert("")
			}else if(!reg.test(this.username)){
				alert('')
				return
			}else if(!c.test(this.password)){
				alert('')
				return
			}
			else {
				let data = {
					'username': this.username,
					'password': this.password
				}
				//
				this.$http.post('./src/login.php', data).then((res) => {
					console.log(res)
					//(-10)
					if(res.data == -1) {
						this.prompting = ''
						this.showPrompting = true
					} else if(res.data == 0) {
						this.prompting = ''
						this.showPrompting = true
					} else if(res.data == 'admin') {
						//
						this.$router.push('/main')
					} else {
						this.prompting = ''
						this.showPrompting = true
						setCookie('username', this.username, 1000 * 60)
						setTimeout(function() {
							this.$router.push('/breakfast')
						}.bind(this), 1000)
					}
				})
			}
		}
	}
}



