import {setCookie,getCookie,delCookie} from '../assets/js/cookie.js'
export default {
	data() {
		return {
			name:'',
			img: "./src/assets/img/portrait.jpg",
			nickname: '',
			grade: '',
			age: '22',
			declaration: ''
		}
	},
	mounted(){
		//Cookie,
		let uname=getCookie('username')
		this.name=uname
		//CooKie
		if(uname==''){
			this.$router.push('/')
		}
	},
	methods:{
		quit(){
			//Cookie
			delCookie('username')
		}
	},
	components: {}
}



